.programs{
  margin: 80px auto;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
}
.program img{
  width: 100%;
  border-radius: 10px;
  display: block;
}
.program{
  flex-basis: 31%;
  position: relative;
}
.caption{
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
background: rgba(0,15, 152, 0.3 );
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
color: #fff;
cursor: pointer;
opacity: 0;

padding-top: 70%;
transition: 0.4s;
}
.caption .icon{
  width: 60px;
  height: 60px;
  margin-bottom: 10px;

}
.program:hover .caption{
  opacity: 1;
  padding-top:0 ;
}
@media (max-width: 650px)
{
  .programs{
    flex-direction: column;
  }
  .program{
    flex-basis: 100%;
  }
}
